.project{
  position: relative;
  margin-top: 50px;
  &::after{
    content: '';
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 0;
    right: 0;
    height: 2px;
    background-color: var(--orange);
    opacity: 0.2;
  }
  
  &__container{
    display: flex;
    justify-content: end;
    align-items: center;
      &::before{
        content: '';
        position: absolute;
        z-index: -1;
        width: 2px;
        top: -200px;
        bottom: -20000px;
        left: calc(100% - 26px);
        background-color: var(--orange);
        opacity: 0.2;
      }
    }
  &__title{
    font-size: rem(16);
    color: var(--orange);
    &>span{
      font-size: rem(14);
      color: var(--white);
    }
    @media(max-width:767px){
      display: none;
    }
  }
}
.o-block{
  position: relative;
  width: 30px;
  height: 30px;
  background-color: var(--orange);
  box-shadow: 0px 0px 30px 5px rgba(255, 185, 0, 1);
  &::before{
    content: '';
    position: absolute;
    top: -5px;
    left: -5px;
    width: calc(100% + 10px);
    height: calc(100% + 10px);
    border: 1px solid var(--orange);
  }
  &.project__block{
    margin-left: 30px;
  }
}